import React from "react";
import { EmptyTip } from "./EmptyTip";
import { LoadingTip } from "./LoadingTip";
import { ErrorTip } from "./ErrorTip";
import { FoundTip } from "./FoundTip";
import { StyledProps } from "../_type";
import { withStatics } from "../_util/with-statics";

export interface StatusTipProps extends StyledProps {
  status: "empty" | "loading" | "found" | "error";

  /**
   * 
   * @default ""
   */
  emptyText?: React.ReactNode;

  /**
   * 
   * @default "..."
   */
  loadingText?: React.ReactNode;

  /**
   * 
   * @default ""
   */
  errorText?: React.ReactNode;

  /**
   * 
   *  `status = error"`  `onRetry` 
   * @default ""
   */
  retryText?: React.ReactNode;

  /**
   * 
   * @default ""
   */
  foundText?: React.ReactNode;

  /**
   * 
   *  `status = found"`  `onClear` 
   * @default ""
   */
  clearResultText?: React.ReactNode;

  /**
   * `status = "found"`  `clearResultText` 
   */
  onClear?: () => void;

  /**
   * `status = "error"`  `retryText` 
   */
  onRetry?: () => void;

  /**
   * 
   * @default false
   */
  hideIcon?: boolean;
}

export const StatusTip = withStatics(
  function StatusTip({
    status,
    emptyText,
    loadingText,
    errorText,
    foundText,
    clearResultText,
    retryText,
    onClear,
    onRetry,
    ...styledProps
  }: StatusTipProps) {
    switch (status) {
      case "loading":
        return <LoadingTip {...styledProps} loadingText={loadingText} />;
      case "empty":
        return <EmptyTip {...styledProps} emptyText={emptyText} />;
      case "found":
        return (
          <FoundTip
            {...styledProps}
            foundText={foundText}
            onClear={onClear}
            clearResultText={clearResultText}
          />
        );
      case "error":
        return (
          <ErrorTip
            {...styledProps}
            errorText={errorText}
            retryText={retryText}
            onRetry={onRetry}
          />
        );
    }
    return null;
  },
  {
    LoadingTip,
    EmptyTip,
    FoundTip,
    ErrorTip,
  }
);
