import { isCallable } from "./is-callable";

/**
 *  `R | (...args) => R` 
 * ts <= 3.2  typeof x === "function" 
 */
export function injectValue<P extends unknown[], R = React.ReactNode>(
  target: R | ((...args: P) => R)
) {
  return (...args: P) => (isCallable(target) ? target(...args) : target);
}
