import React from "react";
import classNames from "classnames";
import { Tooltip } from "../tooltip";
import { Icon } from "../icon";
import { StyledProps, Combine, Omit } from "../_type";
import { forwardRefWithStatics } from "../_util/forward-ref-with-statics";
import { useConfig } from "../_util/config-context";

/**
 * Button 
 *
 *  `<button>` 
 */
export interface ButtonProps
  extends Combine<
    StyledProps,
    Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, "type">
  > {
  /**
   * 
   *
   *   - `primary` 
   *   - `weak` 
   *   - `pay` 
   *   - `text` 
   *   - `link` 
   *   - `icon` 
   *
   * @default "weak"
   */
  type?: "primary" | "weak" | "pay" | "text" | "link" | "icon";

  /**
   *  `<button>`  `type` 
   */
  htmlType?: React.ButtonHTMLAttributes<HTMLButtonElement>["type"];

  /**
   *  `icon` 
   *
   * ____ `icon` `children` 
   */
  icon?: string;

  /**
   * 
   *
   *  `onClick` 
   * */
  disabled?: boolean;

  /**
   * 
   */
  loading?: boolean;

  /**
   * 
   */
  onClick?: (e?: React.MouseEvent) => void;

  /**
   *  Tooltip  [Tooltip](/component/tooltip) 
   */
  tooltip?: React.ReactNode;
}

export const Button = forwardRefWithStatics(
  (props: ButtonProps, ref: React.Ref<HTMLButtonElement>) => {
    const {
      disabled,
      loading: _loading,
      icon: _icon,
      type: _type,
      className,
      style,
      onClick,
      children,
      tooltip,
      htmlType,
      ...buttonProps
    } = props;

    const { classPrefix } = useConfig();

    let [loading, icon, type] = [_loading, _icon, _type];

    // 
    if (!type) {
      type = icon ? "icon" : "weak";
    }

    //  loading
    if (
      ["primary", "weak", "pay"].indexOf(type) === -1 &&
      typeof loading !== "undefined"
    ) {
      loading = false;
    }

    // 
    const btnClassName = `${classPrefix}-btn`;
    const classList: (string | object)[] = [`${classPrefix}-btn`];

    // 
    if (type !== "primary") {
      classList.push(`${btnClassName}--${type}`);
    }

    // 
    classList.push({
      "is-disabled": disabled,
      "is-loading": loading,
    });

    // 
    if (className) {
      classList.push(className);
    }

    // loading  loading 
    if (loading) {
      icon = "loading";
    }

    /* eslint-disable react/button-has-type */
    let button: JSX.Element = (
      <button
        ref={ref}
        className={classNames(...classList)}
        onClick={(!disabled && !loading && onClick) || null}
        style={style || {}}
        type={htmlType}
        disabled={disabled}
        {...buttonProps}
      >
        {icon ? <Icon type={icon} /> : children}
      </button>
    );
    /* eslint-enable react/button-has-type */

    if (tooltip) {
      button = <Tooltip title={tooltip}>{button}</Tooltip>;
    }

    return button;
  },
  {
    defaultLabelAlign: "middle",
  }
);
