import React, { useEffect, useState, useRef } from "react";
import ReactDOM from "react-dom";
import { Modal, ModalProps } from "./ModalMain";
import { Button } from "../button";
import { ModalMessage } from "./ModalMessage";
import { Combine, Omit } from "../_type";
import { callBoth } from "../_util/call-both";
import { useTranslation } from "../i18n";
import { mergeRefs } from "../_util/merge-refs";
import { ConfigProvider } from "../configprovider";

/**
 * API 
 * @param options
 * @returns  true  false 
 */
export function alert(options: AlertOptions) {
  return new Promise<void>(resolve => {
    const el = document.createElement("div");
    ReactDOM.render(
      <ConfigProvider>
        <ModalAlertAPI
          {...options}
          onButtonClick={() => resolve()}
          onExited={() => {
            ReactDOM.unmountComponentAtNode(el);
          }}
        />
      </ConfigProvider>,
      el
    );
  });
}

export interface TypedAlertOptions extends Omit<AlertOptions, "type"> {}

/**
 * API 
 */
export function success(options: TypedAlertOptions) {
  return alert({ type: "success", ...options });
}

/**
 * API 
 */
export function error(options: TypedAlertOptions) {
  return alert({ type: "error", ...options });
}

/**
 *  `Modal.success``Modal.error`  `Modal.alert` 
 */
export interface AlertOptions {
  /**
   * 
   */
  type?: "error" | "warning" | "success" | "pending" | "infoblue";

  /**
   * 
   */
  message: React.ReactNode;

  /**
   * 
   */
  description?: React.ReactNode;

  /**
   * 
   *
   * 
   */
  buttonText?: React.ReactNode;

  /**
   * 
   *
   *  `buttonText` 
   */
  buttons?: React.ReactElement[];

  /**
   *  ModalProps  `size` 
   */
  size?: ModalProps["size"];

  /**
   * 
   * @default true
   */
  maskClosable?: boolean;
}

function ModalAlertAPI({ onButtonClick, ...modalProps }: ModalAlertProps) {
  const [visible, setVisible] = useState(false);

  // 
  useEffect(() => setVisible(true), []);

  const close = () => {
    setVisible(false);
    onButtonClick();
  };

  return (
    <ModalAlert
      visible={visible}
      onButtonClick={close}
      onClose={close}
      {...modalProps}
    />
  );
}

export interface ModalAlertProps extends Combine<ModalProps, AlertOptions> {
  onButtonClick(): void;
}

/**
 *  ModalAlert 
 *  `Modal.success()` / `Modal.error()` API 
 */
export function ModalAlert({
  type,
  message,
  description,
  ...modalProps
}: ModalAlertProps) {
  useEffect(() => {
    window.focus();
  }, []);
  return (
    <Modal disableCloseIcon {...modalProps}>
      <Modal.Body>
        <ModalMessage icon={type} message={message} description={description} />
      </Modal.Body>
      <Modal.Footer>
        <ModalButton {...modalProps} />
      </Modal.Footer>
    </Modal>
  );
}

function ModalButton({
  buttons,
  buttonText,
  onButtonClick,
}: Partial<ModalAlertProps>) {
  const t = useTranslation();
  const mainButtonRef = useRef<HTMLButtonElement>(null);
  useEffect(() => {
    if (mainButtonRef.current) {
      mainButtonRef.current.focus();
    }
  }, []);

  const okText = buttonText || t.okText;

  return (
    <>
      {(buttons || [<Button type="primary">{okText}</Button>]).map(
        (button, index) => {
          return React.cloneElement(button, {
            key: index,
            onClick: callBoth(button.props.onClick, onButtonClick),
            ref:
              index === 0
                ? mergeRefs(button.props.ref, mainButtonRef)
                : button.props.ref,
          });
        }
      )}
    </>
  );
}
