import React, { useEffect } from "react";
import { Button } from "../button";
import { Overlay } from "../overlay";
import { MediaObject } from "../mediaobject";
import { BubbleProps, BubbleContent } from "../bubble";
import { useConfig } from "../util";
import { injectValue } from "../_util/inject-value";
import { useDefault } from "../_util/use-default";
import { useTranslation } from "../i18n";

export interface GuideStep {
  /**
   * 
   */
  element: Element | (() => Element);

  /**
   * 
   */
  placement?: BubbleProps["placement"];

  /**
   * 
   */
  title?: React.ReactNode;

  /**
   * 
   */
  description?: React.ReactNode;

  /**
   *  URL
   */
  image?: string;
}

export interface GuideProps {
  /**
   * 
   */
  steps: GuideStep[];

  /**
   *  `children`  `children` 
   */
  children?: JSX.Element;

  /**
   * 
   */
  current?: number;

  /**
   * 
   */
  onCurrentChange?: (current: number) => void;

  /**
   * 
   * @default true
   */
  visible?: boolean;

  /**
   * 
   * @default false
   */
  hideCancelButton?: boolean;

  /**
   * 
   * @default false
   */
  showBackButton?: boolean;

  /**
   * 
   * @default ""
   */
  cancelText?: string;

  /**
   * 
   * @default ""
   */
  backText?: string;

  /**
   * 
   * @default ""
   */
  nextText?: string;

  /**
   * 
   * @default ""
   */
  finishText?: string;

  /**
   * 
   * @default false
   */
  autoScrollIntoView?: boolean;
}

const borderWidth = 1;

function getOffset(element: HTMLElement) {
  if (!element) {
    return {};
  }
  const { body } = document;
  const docEl = document.documentElement;
  const scrollTop = window.pageYOffset || docEl.scrollTop || body.scrollTop;
  const scrollLeft = window.pageXOffset || docEl.scrollLeft || body.scrollLeft;
  const x = element.getBoundingClientRect();
  return {
    top: x.top + scrollTop,
    width: x.width,
    height: x.height,
    left: x.left + scrollLeft,
  };
}

export function Guide({
  steps = [],
  children = null,
  visible = true,
  autoScrollIntoView,
  current,
  onCurrentChange,
  showBackButton,
  hideCancelButton,
  cancelText,
  backText,
  nextText,
  finishText,
}: GuideProps) {
  const t = useTranslation();
  const { classPrefix } = useConfig();
  const [currentIndex, setCurrentIndex] = useDefault(
    current,
    -1,
    onCurrentChange
  );

  // 
  useEffect(() => {
    if (typeof current === "undefined") {
      setCurrentIndex(0);
    }
  }, [current, setCurrentIndex]);

  const step = steps[currentIndex];

  useEffect(() => {
    if (step) {
      const element = injectValue(step.element)();
      if (element) {
        element.scrollIntoView({
          behavior: "smooth",
          block: "center",
          inline: "center",
        });
      }
    }
  }, [autoScrollIntoView, step]);

  if (!visible || !step) {
    return null;
  }

  const count = steps.length;
  const element = injectValue(step.element)();
  const { title, description, image, placement } = step;
  const { height, width } = getOffset(element as HTMLElement);

  return (
    <Overlay
      layers={[
        <Overlay.Layer
          key="highlight"
          visible
          placement="top-start"
          placementOffset={0}
          modifiers={{
            flip: { enabled: false },
            preventOverflow: { enabled: false },
          }}
          content={({ style }) => {
            const left = +style.left;
            const top = +style.top;
            const p = `M ${left - borderWidth} ${top - borderWidth} H ${left +
              width +
              borderWidth} V ${top + height + borderWidth} H ${left -
              borderWidth} L ${left - borderWidth} ${top - borderWidth} Z`;
            return (
              <svg
                style={{
                  top: 0,
                  left: 0,
                  overflow: "hidden",
                  pointerEvents: "none",
                  position: "fixed",
                  width: "100vw",
                  height: "100vh",
                  zIndex: 9999,
                  fillRule: "evenodd",
                }}
              >
                <path
                  d={`${p} M 0 0 H 9999 V 9999 H 0 L 0 0 Z`}
                  fill="#000000"
                  opacity={0.5}
                />
                <path
                  d={`M ${left} ${top} H ${left + width} V ${top +
                    height} H ${left} L ${left} 0 Z ${p}`}
                  fill="#cccccc"
                  opacity={1}
                />
              </svg>
            );
          }}
        />,
        <Overlay.Layer
          key="bubble"
          visible
          placement={placement}
          placementOffset={10}
          modifiers={{
            flip: { enabled: false },
            preventOverflow: { enabled: false },
          }}
          content={({ placement }) => (
            <BubbleContent
              placement={placement}
              className={`${classPrefix}-popconfirm`}
            >
              <div className={`${classPrefix}-popconfirm__body`}>
                <MediaObject media={image && <img src={image} alt="" />}>
                  {title && (
                    <h3 className={`${classPrefix}-popconfirm__messagetitle`}>
                      {title}
                    </h3>
                  )}
                  {description && (
                    <div className={`${classPrefix}-popconfirm__messagetext`}>
                      {description}
                    </div>
                  )}
                </MediaObject>
              </div>
              <div className={`${classPrefix}-popconfirm__footer`}>
                {!hideCancelButton && current < count - 1 && (
                  <Button
                    type="text"
                    onClick={() => {
                      setCurrentIndex(-1);
                    }}
                  >
                    {cancelText || t.guideCancel}
                  </Button>
                )}
                {showBackButton && current > 0 && (
                  <Button
                    type="text"
                    onClick={() => {
                      setCurrentIndex(current - 1);
                    }}
                  >
                    {backText || t.guideBack}
                  </Button>
                )}
                {current < count - 1 ? (
                  <Button
                    type="link"
                    onClick={() => {
                      setCurrentIndex(current + 1);
                    }}
                  >
                    {nextText || t.guideNext} {current + 1}/{count}
                  </Button>
                ) : (
                  <Button
                    type="link"
                    onClick={() => {
                      setCurrentIndex(current === count - 1 ? -1 : current + 1);
                    }}
                  >
                    {finishText || t.guideFinish} {current + 1}/{count}
                  </Button>
                )}
              </div>
            </BubbleContent>
          )}
        />,
      ]}
    >
      {ref => {
        ref(element as HTMLElement);
        return children;
      }}
    </Overlay>
  );
}
