import React from "react";
import classNames from "classnames";
import { StyledProps } from "../_type";
import { FormControlProps, FormControl } from "./FormControl";
import { FormContext } from "./FormContext";
import { useConfig } from "../_util/config-context";

export interface FormItemProps extends StyledProps {
  /**
   * HTML for 
   *
   *  `label` 
   */
  htmlFor?: string;

  /**
   * 
   */
  label?: React.ReactNode;

  /**
   *  Input, CheckGroup 
   * 
   */
  children?: React.ReactNode;

  /**
   * 
   */
  status?: FormControlProps["status"];

  /**
   *  icon
   * @default true
   */
  showStatusIcon?: boolean;

  /**
   * /
   */
  message?: FormControlProps["message"];

  /**
   * 
   *
   * - `auto` 
   * - `top` 
   * - `middle` 
   *
   * @default "auto"
   */
  align?: "auto" | "top" | "middle";

  /**
   * 
   *
   * @default false
   */
  required?: boolean;

  /**
   * 
   */
  suffix?: React.ReactNode;
}

export function FormItem({
  htmlFor,
  label,
  children,
  className,
  style,
  status,
  message,
  align,
  required,
  showStatusIcon = true,
  suffix,
}: FormItemProps) {
  const { classPrefix } = useConfig();
  return (
    <FormContext.Consumer>
      {({ hideLabel }) => (
        <div
          className={classNames(`${classPrefix}-form__item`, className)}
          style={style}
        >
          {!hideLabel && (
            <div
              className={classNames(`${classPrefix}-form__label`, {
                "is-required": required,
              })}
            >
              {typeof label !== "undefined" && (
                <label htmlFor={htmlFor}>{label}</label>
              )}
            </div>
          )}
          <FormControl
            status={status}
            showStatusIcon={showStatusIcon}
            message={message}
            alignLabelTop={isAlignLabelTop(align, children)}
          >
            {children}
            {suffix && (
              <div className={`${classPrefix}-form__help-text--inline`}>
                {suffix}
              </div>
            )}
          </FormControl>
        </div>
      )}
    </FormContext.Consumer>
  );
}

function isAlignLabelTop(
  align: "auto" | "top" | "middle",
  children: React.ReactNode
): boolean {
  if (align === "top") {
    return true;
  }
  if (align === "middle") {
    return false;
  }
  return getDefaultAlign(children) === "top";
}

function getDefaultAlign(control: React.ReactNode) {
  if (
    React.isValidElement(control) &&
    //  function, forwaredRef  object
    (typeof control.type === "function" || typeof control.type === "object") &&
    "defaultLabelAlign" in control.type
  ) {
    return control.type["defaultLabelAlign"]; // eslint-disable-line dot-notation
  }
  return "top";
}
